@charset "UTF-8";

/* 폰트 */
@font-face {
    font-family: "SchoolSafetyHalfMoon";
    src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/2508-2@1.0/HakgyoansimBandalL.woff2") format("woff2");
    font-weight: normal;
    font-style: normal;
}

/* ========================= Root / Reset ========================= */
:root {
    --header-h: 8vh;
    --brand: #b19fff;
    --ink: #111;

    /* ▼ 스킬 칩 관련 변수(누락 보완) */
    --chip: 100px;
    --chip-radius: 50%;
    --icon: 50px;
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    list-style: none;
    font-family: "SchoolSafetyHalfMoon";
    color: #2a2a2a;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    width: 100%;
    background: #F5F5F5;
}

/* ========================= Header ========================= */
header {
    position: fixed;
    width: 100%;
    height: 8vh;
    background: #F5F5F5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    z-index: 9999;
}

.headerBtn {
    font-size: 2rem;
    font-weight: bold;
    background: transparent;
    cursor: pointer;
    letter-spacing: .5px;
}

.headerNav {
    display: flex;
    gap: 100px;
}

.headerNav a {
    font-size: 2rem;
    font-weight: bold;
    text-decoration: none;
    letter-spacing: 1px;
}

/* ========================= Sections (common) ========================= */
#Main,
#Profile,
#Skills,
#Projects,
#Contact {
    width: 100%;
    height: 100vh;
    min-height: calc(100vh - var(--header-h));
    scroll-margin-top: var(--header-h);
}

/* ========================= Main ========================= */
#Main {
    width: 100%;
    margin: 0 auto;
}

/* ===== MainHero ===== */
#Main.hero {
    display: grid;
    place-items: center;
    background: #f5f5f5;
    min-height: calc(100vh - var(--header-h));
    padding-top: var(--header-h);
}

.hero__inner {
    width: min(1280px, 94vw);
    display: grid;
    grid-template-columns: 520px 1fr;
    align-items: center;
    gap: clamp(24px, 6vw, 64px);
}

/* Left image */
.hero__left {
    display: flex;
    justify-content: center;
}

.hero__avatar {
    width: min(520px, 44vw);
    height: auto;
    object-fit: contain;
    filter: drop-shadow(0 20px 40px rgba(0, 0, 0, .12));
}

/* Right copy */
.hero__right {
    color: #111;
}

.hero__quote {
    font-size: 4rem;
    letter-spacing: .02em;
    line-height: 1.2;
}

.hero__quote--sub {
    margin-top: .35em;
    text-align: right;
}

.hero__quote b {
    font-weight: 800;
}

.qmark {
    opacity: .6;
    margin: 0 .12em;
}

.hero__kr {
    margin: clamp(14px, 2.6vw, 28px) 0 clamp(20px, 3vw, 36px);
    font-size: 2.5rem;
    line-height: 1.7;
    letter-spacing: .04em;
}

.hero__kr b {
    font-size: 3rem;
    font-weight: 800;
}

.hero__title {
    font-size: clamp(28px, 4.2vw, 52px);
    font-weight: 600;
    letter-spacing: .04em;
}

.hero__title b {
    font-weight: 900;
}

/* Responsive */
@media (max-width: 980px) {
    .hero__inner {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .hero__quote--sub {
        text-align: center;
    }

    .hero__right {
        align-items: center;
    }

    .hero__avatar {
        width: min(420px, 66vw);
    }
}


/* ========================= Profile ========================= */
#Profile {
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: calc(var(--header-h) + 20px);
}

.ProfileLeft,
.ProfileRight {
    width: 50%;
    min-height: calc(100vh - var(--header-h));
    background: #F5F5F5;
}

.ProfileLeft {
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
}

.P_imgBox {
    position: relative;
    display: flex;
    align-items: flex-end;
    gap: 20px;
    top: 28%;
    left: 50%;
    transform: translate(-30%, 0);
}

.P_img1,
.P_img2,
.P_img3 {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
}

.P_img1 {
    width: 350px;
    height: 450px;
    top: -260px;
    left: 220px;
    z-index: 2;
}

.P_img2 {
    width: 200px;
    height: 300px;
    transform: rotate(15.261deg);
    top: -280px;
    left: 450px;
}

.P_img3 {
    width: 300px;
    height: 400px;
    transform: rotate(-15.261deg);
    top: -290px;
    left: 100px;
}

.Profile_textBox {
    position: absolute;
    bottom: 25%;
    left: 50%;
    transform: translate(-40%, 0);
    width: 850px;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding-left: 10px;
}

.Profile_text1 {
    font-size: 1.5rem;
    font-weight: 600;
    letter-spacing: 2.8px;
}

.Profile_text2 {
    height: 40px;
    line-height: 1;
    font-size: 2rem;
    font-weight: 600;
    letter-spacing: 3px;
    overflow-wrap: anywhere;
}

.ProfileRight {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 50px;
    align-self: center;
}

.aboutBox,
.educationBox,
.trainingBox {
    position: relative;
    top: -10%;
    width: 83%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    border: 2px solid #2a2a2a;
    border-radius: 7px;
}

.aboutTitle,
.educationTitle,
.trainingTitle {
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 0 16px;
    background: #F5F5F5;
    font-weight: 700;
    font-size: 2.5rem;
    letter-spacing: 2px;
    line-height: 1;
    color: #222;
}

.about,
.education,
.training {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.about {
    height: 150px;
}

.education {
    height: 500px;
}

.training {
    height: 130px;
}

.about p,
.education p,
.training p {
    font-size: 1.5rem;
    font-weight: 500;
    letter-spacing: 3px;
}

.about p span,
.education span,
.training span {
    font-size: 1.7rem;
    font-weight: 600;
    letter-spacing: 2px;
}

/* ========================= Skills ========================= */
#Skills {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: visible;
}

/* 내부 컨테이너 */
.skillsWrap {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 100px;
}

/* 제목 */
.skillsTitle {
    font-size: 4rem;
    letter-spacing: 3px;
}

/* 캐러셀 래퍼 */
.skillsCarousel {
    position: relative;
    z-index: 5;
}

/* 스와이프 트랙 */
.skillsTrack {
    position: relative;
    overflow-x: auto;
    overflow-y: visible;
    -webkit-overflow-scrolling: touch;
    scroll-snap-type: x mandatory;
    overscroll-behavior-x: contain;
    scrollbar-width: none;
    padding: 50px 250px;
}

.skillsTrack::-webkit-scrollbar {
    display: none;
}

/* 리스트 */
.skillsList {
    display: flex;
    gap: 30px;
    align-items: center;
}

/* 아이콘 칩 */
.skillChip {
    position: relative;
    z-index: 6;
    flex: 0 0 auto;
    width: var(--chip);
    height: var(--chip);
    border-radius: var(--chip-radius);
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    scroll-snap-align: center;
    transition: transform .18s ease, filter .18s ease, box-shadow .18s ease;
    cursor: pointer;
    user-select: none;
    transform-origin: center;
    writing-mode: horizontal-tb;
    /* 텍스트 세로깨짐 방지 */
}

.skillChip img {
    width: var(--icon);
    height: var(--icon);
    object-fit: contain;
    pointer-events: none;
}

.skillChip span {
    display: block;
    margin-top: 8px;
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: .5px;
    color: #000;
    line-height: 1;
    white-space: nowrap;
    writing-mode: horizontal-tb;
    text-orientation: mixed;
    transition: color .25s ease, transform .25s ease;
}

.skillChip.is-active {
    box-shadow: 0 10px 30px rgba(0, 0, 0, .15);
}

.skillChip:not(.is-active) {
    filter: grayscale(.4) brightness(.9) opacity(.8);
}

/* 설명 카드 */
.skillDetail {
    background: #cfcfcf;
    border-radius: 12px;
    padding: 30px 24px;
    box-shadow: 0 4px 12px rgba(0, 0, 0, .08);
}

.skillName {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 15px;
}

.skillDesc {
    font-size: 1.6rem;
    letter-spacing: 1px;
    line-height: 1.5;
}

/* 점수 바 */
.skillMeter {
    width: 100%;
    max-width: 1200px;
    margin: 26px auto 0;
}

.meterTrack {
    height: 20px;
    background: var(--ink);
    border-radius: 999px;
    overflow: hidden;
}

.meterFill {
    display: block;
    height: 100%;
    width: 0%;
    background: var(--brand);
    border-top-right-radius: 999px;
    border-bottom-right-radius: 999px;
    transition: width .7s cubic-bezier(.22, .9, .24, 1);
}

.meterScore {
    text-align: right;
    font-size: 1rem;
    margin-top: 6px;
    color: var(--ink);
}

.meterVal {
    font-weight: 700;
}

/* ========================= Projects ========================= */
#Projects,
#Project02,
#Project03,
#Project04,
#Project05 {
    width: 100%;
    min-height: calc(100vh - var(--header-h));
    display: flex;
    align-items: center;
    justify-content: center;
    background: #F5F5F5;
    padding: 30px 20px;
}

.projWrap {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
}

.projHead {
    text-align: left;
    font-size: 4rem;
    letter-spacing: .2rem;
    margin-bottom: 30px;
}

.projGrid {
    display: grid;
    grid-template-columns: 1.3fr .9fr;
    align-items: center;
    gap: 40px;
}

.device {
    width: 100%;
    height: 600px;
    aspect-ratio: 16/10;
    border-radius: 18px;
    background: linear-gradient(#0b0b0b, #151515);
    padding: 18px 16px 24px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, .2);
    position: relative;
}

.device-topbar {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 110px;
    height: 14px;
    background: #0b0b0b;
    border-radius: 0 0 10px 10px;
    display: grid;
    place-items: center;
}

.device-topbar .cam {
    width: 6px;
    height: 6px;
    background: #0d1720;
    border-radius: 50%;
    box-shadow: 0 0 6px rgba(69, 170, 255, .35) inset;
}

.screen {
    width: 100%;
    height: 100%;
    border-radius: 12px;
    overflow: hidden;
    background: #fff;
}

.screen img,
.screen iframe,
.screen video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: 0;
    display: block;
}

.device-foot {
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 70%;
    height: 10px;
    border-radius: 0 0 12px 12px;
    background: linear-gradient(#777, #333);
    filter: blur(.6px);
}

.projCard {
    width: 450PX;
    height: 800px;
    background: #cfcfcf;
    border-radius: 14px;
    padding: 22px 24px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, .08);
}

.projTitle {
    font-size: 3rem;
    font-weight: 800;
    margin-bottom: 20px;
    text-align: center;
}

.projBody {
    font-size: 1.7rem;
    line-height: 1.7;
    letter-spacing: .02rem;
    text-align: left;
    margin-bottom: 16px;
}

.projInfo {
    display: grid;
    gap: 6px;
    margin: 10px 0 30px;
    font-size: 1.5rem;
}

.projInfo b {
    font-weight: 800;
}

.projTech {
    display: flex;
    gap: 12px;
    align-items: center;
    margin-bottom: 30px;
}

.projTech img {
    width: 32px;
    height: 32px;
    object-fit: contain;
}

.projBtns {
    display: flex;
    gap: 12px;
    justify-content: flex-end;
    margin-top: 20px;
}

.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 10px 15px;
    border-radius: 10px;
    background: var(--brand);
    color: #fff;
    font-size: 1.2rem;
    letter-spacing: 2px;
    font-weight: 700;
    text-decoration: none;
    transition: transform .12s ease, opacity .2s;
}

.btn:hover {
    transform: translateY(-1px);
}

.btn.ghost {
    background: transparent;
    box-shadow: inset 0 0 0 2px var(--brand);
    color: #2a2a2a;
}

.btn.ghost:hover {
    opacity: .85;
}

/* Project03(Phone) */
#Project03 {
    width: 100%;
    min-height: calc(100vh - var(--header-h));
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f5f5f5;
    padding: 60px 20px;
}

.device-phone {
    position: relative;
    width: 500px;
    height: 800px;
    border-radius: 40px;
    background: linear-gradient(145deg, #111, #222);
    padding: 18px;
    box-shadow: 0 12px 30px rgba(0, 0, 0, .25);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.phone-notch {
    position: absolute;
    top: 12px;
    left: 50%;
    transform: translateX(-50%);
    width: 130px;
    height: 22px;
    background: #000;
    border-radius: 10px;
}

.phone-screen {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 30px;
    overflow: hidden;
    background: #fff;
    margin-top: 22px;
}

.phone-screen img,
.phone-screen iframe {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: none;
}

.phone-btn {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 90px;
    height: 4px;
    background: #333;
    border-radius: 3px;
}

#Project03 .projCard {
    width: 600PX;
    height: 700px;
    background: #cfcfcf;
}

#Project03 .projCard b {
    color: #000;
}

/* ========================= Contact ========================= */
#Contact {
    width: 100%;
    min-height: calc(100vh - var(--header-h));
    display: flex;
    align-items: center;
    justify-content: center;
    background: #F5F5F5;
    padding: 60px 20px;
}

.contactWrap {
    width: 100%;
    max-width: 1100px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1.2fr;
    gap: 40px;
    align-items: center;
}

.contactImg {
    width: 460px;
    max-width: 100%;
    aspect-ratio: 1/1;
    border-radius: 50%;
    background: #2a2a2a;
    box-shadow: 0 8px 24px rgba(0, 0, 0, .18) inset, 0 8px 24px rgba(0, 0, 0, .06);
    overflow: hidden;
    justify-self: center;
}

.contactAvatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.contactText {
    display: flex;
    flex-direction: column;
    gap: 22px;
}

.contactTitle {
    font-size: 4rem;
    letter-spacing: .08em;
    font-weight: 800;
}

.contactList {
    display: grid;
    gap: 12px;
}

.contactList li {
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 3rem;
}

.contactList a {
    color: #2a2a2a;
    text-decoration: none;
}

.contactList a:hover {
    text-decoration: underline;
}

.ic {
    width: 22px;
    height: 22px;
    display: inline-block;
    position: relative;
    filter: drop-shadow(0 1px 1px rgba(0, 0, 0, .12));
}

.ic.mail::before {
    content: "";
    position: absolute;
    inset: 3px 2px 6px 2px;
    border: 2px solid #2a2a2a;
    border-top: none;
    border-radius: 3px;
}

.ic.mail::after {
    content: "";
    position: absolute;
    left: 3px;
    right: 3px;
    top: 7px;
    height: 0;
    border-top: 2px solid #2a2a2a;
    transform: skewY(-20deg);
    box-shadow: inset 0 0 0 2px #2a2a2a;
}

.ic.phone::before {
    content: "";
    position: absolute;
    inset: 1px;
    border-radius: 6px;
    border: 2px solid #2a2a2a;
}

.ic.phone::after {
    content: "";
    position: absolute;
    left: 7px;
    top: 3px;
    width: 8px;
    height: 16px;
    border-left: 2px solid #2a2a2a;
    border-right: 2px solid #2a2a2a;
    opacity: .001;
}

.ic.insta {
    background: radial-gradient(circle at 70% 30%, #2a2a2a 2px, transparent 3px) no-repeat, conic-gradient(#2a2a2a 0 360deg);
    -webkit-mask: radial-gradient(circle 6px at 50% 50%, #000 98%, transparent 100%) padding-box, none;
    mask: radial-gradient(circle 6px at 50% 50%, #000 98%, transparent 100%) padding-box, none;
    border-radius: 6px;
}

.contactMsg {
    font-size: 1.5rem;
    margin-top: 6px;
    color: #333;
    line-height: 1.7;
    letter-spacing: .02rem;
}

/* ========================= Responsive (global) ========================= */
@media (max-width:980px) {
    .projGrid {
        grid-template-columns: 1fr;
        gap: 26px;
    }

    .projHead {
        font-size: 2.2rem;
    }
}

/* ========================= Right bullets (Projects quick nav) ========================= */
.rb-nav {
    position: fixed;
    right: 22px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 9998;
    padding: 8px 10px;
    border-radius: 18px;
    backdrop-filter: blur(6px);
}

.rb-nav ul {
    display: grid;
    gap: 10px;
}

.rb-dot {
    position: relative;
    width: 20px;
    height: 20px;
    border-radius: 999px;
    background: #e6e6e6;
    border: 0;
    display: grid;
    place-items: center;
    cursor: pointer;
    transition: transform .15s ease, background .15s ease;
}

.rb-dot__inner {
    width: 10px;
    height: 10px;
    border-radius: 999px;
    background: #777;
    transition: background .15s ease, transform .15s ease;
}

.rb-tooltip {
    position: absolute;
    right: 150%;
    top: 50%;
    transform: translateY(-50%) scale(0.8);
    background: rgba(0, 0, 0, .8);
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 1.5px;
    padding: 4px 8px;
    border-radius: 6px;
    white-space: nowrap;
    opacity: 0;
    pointer-events: none;
    transition: all .2s ease;
}

.rb-dot:hover .rb-tooltip {
    opacity: 1;
    transform: translateY(-50%) scale(1);
}

.rb-dot.is-active {
    background: var(--brand);
    box-shadow: 0 0 0 4px rgba(177, 159, 255, .18);
}

.rb-dot.is-active .rb-dot__inner {
    background: #fff;
    transform: scale(1.15);
}

.rb-dot.is-active .rb-tooltip {
    background: var(--brand);
}


@media (max-width:780px) {
    .rb-nav {
        right: 10px;
    }
}

@media (max-width:540px) {
    .rb-nav {
        right: 8px;
        padding: 6px 8px;
    }

    .rb-dot {
        width: 12px;
        height: 12px;
    }

    .rb-dot__inner {
        width: 5px;
        height: 5px;
    }
}


/* ===== Cursor hint (projects only) ===== */
.cursor-hint {
    position: fixed;
    left: 0;
    top: 0;
    /* transform로 위치 이동 */
    transform: translate(-9999px, -9999px);
    z-index: 9999;
    pointer-events: none;
    padding: 8px 12px;
    font-size: 17px;
    letter-spacing: .4px;
    color: #111;
    background: #ffffffcc;
    /* 살짝 투명 */
    border: 1px solid #e6e6e6;
    border-radius: 10px;
    box-shadow: 0 6px 16px rgba(0, 0, 0, .12);
    backdrop-filter: blur(6px);
    opacity: 0;
    transition: opacity .2s ease;
}

.cursor-hint.is-show {
    opacity: 1;
}

.cursor-hint::after {
    content: "";
    position: absolute;
    left: 6px;
    top: -6px;
    border: 6px solid transparent;
    border-bottom-color: #ffffffcc;
    filter: drop-shadow(0 -1px 0 #e6e6e6);
}